
import React from "react";
import { Button, Col, Form, Image, Input, Row, Select, Upload } from "antd";

const { Option } = Select;

const getBase64 = (img, callback) => {
    const reader = new FileReader();
    reader.addEventListener("load", () => callback(reader.result));
    reader.readAsDataURL(img);
};

const beforeUpload = (file) => {
    const isJpgOrPng = file.type === "image/jpeg" || file.type === "image/png";
    if (!isJpgOrPng) {
        alert("You can only upload JPG/PNG file!");
    }
    const isLt2K = file.size / 1024 < 5;
    if (!isLt2K) {
        alert("Hình ảnh phải có kích thước nhỏ hơn 5Kb");
    }
    return isJpgOrPng && isLt2K;
}

const TabStaffInformation = ({ listGender, fileImage, setFileImage }) => {
    const listTeam = [
        { value: 1, label: "Back-End" },
        { value: 2, label: "Front-End" },
        { value: 3, label: "Tester" }
    ];

    const handleChange = (info) => {
        if (info.file.status === "uploading") {
            return;
        }
        if (info.file.status === "done") {
            getBase64(info.file.originFileObj, (url) => {
                setFileImage(url);
            });
        }
    };

    return (
        <Row justify="space-between">
            <Col span={16}>
                <Row gutter={[16, 0]}>
                    <Col className="gutter-row" span={8}>
                        <Form.Item
                            label='Tên nhân viên'
                            name="fullName"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bắt buộc phải nhập trường này!',
                                },
                                {
                                    pattern: '^([a-zA-Z ])*$',
                                    message: 'Chỉ chứa chữ không dấu',
                                },
                            ]}
                        >
                            <Input style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>
                    <Col className="gutter-row" span={8}>
                        <Form.Item
                            label="Mã nhân viên"
                            name="code"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bắt buộc phải nhập trường này!',
                                },
                                { whitespace: true, message: 'không được để trống' },
                                { min: 6, message: 'Tối thiểu 6 kí tự', },
                                { max: 10, message: 'Tối đa 10 kí tự' }
                            ]}
                        >
                            <Input style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>
                    <Col className="gutter-row" span={8}>
                        <Form.Item
                            name="gender"
                            label="Giới tính"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bắt buộc phải chọn trường này!',
                                },
                            ]}
                        >
                            <Select
                                placeholder="Giới tính"
                            >
                                {listGender.map(item => {
                                    return <Option key={item.value} value={item.value}>{item.label}</Option>;
                                })}
                            </Select>
                        </Form.Item>
                    </Col>
                    <Col className="gutter-row" span={8}>
                        <Form.Item
                            label="Ngày sinh"
                            name="dateOfBirth"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bắt buộc phải chọn trường này!',
                                },
                            ]}
                        >
                            <Input type="date" style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>
                    <Col className="gutter-row" span={8}>
                        <Form.Item
                            label="Số điện thoại"
                            name="phone"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bắt buộc phải nhập trường này!',
                                },
                                { min: 10, message: 'Tối thiểu 10 số' },
                                { max: 11, message: 'Tối đa 11 số' },
                                {
                                    pattern: '^([0-9][0-9]*|0)$',
                                    message: 'Trường này phải là số',
                                }
                            ]}
                        >
                            <Input style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>

                    <Col className="gutter-row" span={8}>
                        <Form.Item
                            label='Nhóm'
                            name="teamId"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bắt buộc phải chọn trường này!',
                                },
                            ]}
                        >
                            <Select
                                placeholder="Chọn nhóm"
                            >
                                {listTeam?.map(item => {
                                    return <Option key={item.value} value={item.value}>{item.label}</Option>;
                                })}
                            </Select>
                        </Form.Item>
                    </Col>
                    <Col className="gutter-row" span={12}>
                        <Form.Item
                            label='Mã CCCD'
                            name="citizenId"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bắt buộc phải nhập trường này!',
                                },
                                {
                                    pattern: '^([0-9][0-9]*|0)$',
                                    message: 'Trường này phải là số',
                                },
                                { min: 9, message: 'Tối thiểu 9 số' },
                                { max: 12, message: 'Tối đa 12 số' },
                            ]}
                        >
                            <Input style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>
                    <Col className="gutter-row" span={12}>
                        <Form.Item
                            label="Email"
                            name="email"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bắt buộc phải nhập trường này!',
                                },
                                {
                                    type: 'email',
                                    message: 'Không đúng định dạng email!',
                                }
                            ]}
                        >
                            <Input style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>

                    <Col className="gutter-row" span={24}>
                        <Form.Item
                            label='Địa chỉ cụ thể'
                            name="address"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bắt buộc phải nhập trường này!',
                                },
                                { whitespace: true, message: 'không được để trống' },
                            ]}
                        >
                            <Input style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>

                </Row>
            </Col>
            <Col span={8} className="image-info">
                <Row>
                    <div className="avatar" >
                        <Image
                            src={fileImage ? fileImage?.toString() : "error"}
                            fallback="data:image/png;base64,"
                        />
                    </div>
                </Row>
                <Row >
                    <Form.Item name="photoUrl">
                        <Upload
                            name="photoUrl"
                            action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
                            onChange={handleChange}
                            beforeUpload={beforeUpload}
                        >
                            <Button >
                                Thêm ảnh đại diện
                            </Button>
                        </Upload>
                    </Form.Item>
                </Row>
            </Col>
        </Row >
    )
}
export default TabStaffInformation;